import React, { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import Header from './header';
import styles from "../styles/admindashboard.module.css"
import { useAuth } from './users/AuthContext';

type User = {
  uid: number;
  name: string;
  email: string;
};

type Post = {
  pid: number;
  title: string;
  content: string;
  category: string;
};

type Comment = {
  comid: number;
  pid: number;
  uid: number;
  content: string;
  createdAt: number;
  username: string;
};

type Category = {
  cid: number;
  name: string;
};

const AdminDashboard: React.FC = () => {
  const { user } = useAuth();
  const [users, setUsers] = useState<User[]>([]);
  const [posts, setPosts] = useState<Post[]>([]);
  const [comments, setComments] = useState<Comment[]>([]);
  const [categories, setCategories] = useState<Category[]>([]);
  const [showUsers, setShowUsers] = useState(false);
  const [showPosts, setShowPosts] = useState(false);
  const [showComments, setShowComments] = useState(false);
  const [showCategories, setShowCategories] = useState(false);

  const fetchUsers = useCallback(async () => { 
    try {
      const response = await axios.get('/api/admin/users');
      setUsers(response.data.users);
    } catch (error) {
      console.error(error);
    }
  }, []);

  const fetchPosts = useCallback(async () => { 
    try {
      const response = await axios.get('/api/admin/posts');
      setPosts(response.data.posts);
    } catch (error) {
      console.error(error);
    }
  }, []);

  const fetchComments = useCallback(async () => { // useCallback 
    try {
      const response = await axios.get('/api/admin/comments');
      setComments(response.data.comments);
    } catch (error) {
      console.error(error);
    }
  }, []);

  const fetchCategories = useCallback(async () => { // useCallback 
    try {
      const response = await axios.get('/api/admin/categories');
      setCategories(response.data.categories);
    } catch (error) {
      console.error(error);
    }
  }, []);

  useEffect(() => {
    if (user && !user.isAdmin) {
      alert('  .');
    }
    fetchUsers();
    fetchPosts();
    fetchComments();
    fetchCategories();
  }, [user, fetchUsers, fetchPosts, fetchComments, fetchCategories]);

  const toggleUsers = () => {
    setShowUsers(!showUsers);
  };

  const togglePosts = () => {
    setShowPosts(!showPosts);
  };

  const toggleComments = () => {
    setShowComments(!showComments);
  };

  const toggleCategories = () => {
    setShowCategories(!showCategories);
  };





  const deleteUser = async (uid: number) => {
    if (!window.confirm('   ?')) return;
    try {
      await axios.delete(`/api/admin/users/${uid}`);
      fetchUsers(); //      
    } catch (error) {
      console.error(error);
    }
  };

  const deleteComment = async (comid: number) => {
    if (!window.confirm('   ?')) return;
    try {
      await axios.delete(`/api/admin/comments/${comid}`);
      fetchComments(); //      
    } catch (error) {
      console.error(error);
    }
  };

  const deleteCategory = async (cid: number) => {
    if (!window.confirm('   ?')) return;
    try {
      await axios.delete(`/api/admin/categories/${cid}`);
      fetchCategories(); //      
    } catch (error) {
      console.error(error);
    }
  };
  const deletePost = async (pid: number) => {
    console.log(pid);

    if (!window.confirm('   ?')) return;
    try {
      await axios.delete(`/api/admin/posts/${pid}`);
      fetchPosts(); //      
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <>
      <Header />
      {user && user.isAdmin && (
        <div className={styles.dashboard}>
          <h2> </h2>
  
          <div className={styles.section} onClick={toggleUsers}>
            <h3> </h3>
          </div>
          {showUsers && users.map((user, index) => (
            <div key={user.uid + user.name + index} className={styles.userinfo}>
              <p>{user.name} ({user.email})</p>
              <button onClick={() => deleteUser(user.uid)}></button>
            </div>
          ))}
  
          <div className={styles.section} onClick={togglePosts}>
            <h3> </h3>
          </div>
          {showPosts &&
            posts.map((post, index) => (
              <div key={post.pid + post.title + index} className={styles.postinfo}>
                <p>{post.title}</p>
                <button onClick={() => deletePost(post.pid)}></button>
              </div>
            ))}
  
          <div className={styles.section} onClick={toggleComments}>
            <h3> </h3>
          </div>
  
          {showComments && comments.map((comment) => (
            <div key={comment.comid} className={styles.commentinfo}>
              <p>{comment.username} ({new Date(comment.createdAt)
                .toLocaleString("ko-KR", { timeZone: 'UTC', year: "numeric", month: "long", day: "numeric" })}): {comment.content}</p>
              <button onClick={() => deleteComment(comment.comid)}></button>
            </div>
          ))}
  
          <div className={styles.section} onClick={toggleCategories}>
            <h3> </h3>
          </div>
          {showCategories && categories.map((category, index) => (
            <div key={category.cid + category.name + index} className={styles.categoryinfo}>
              <p>{category.name}</p>
              <button onClick={() => deleteCategory(category.cid)}></button>
            </div>
          ))}
        </div>
      )}
    </>
  );
  
};

export default AdminDashboard;